import React from "react";
import { Link } from "react-router-dom";

const Navbar = () => {
  return (
    <header>
      <div className="container">
        <div className="row">
          <div className="col-md-12">
            {/* <!-- logo begin --> */}
            <div id="logo">
              <Link to="/">
                <h2>YOJAKAJ EVENTS</h2>
              </Link>
            </div>
            {/* <!-- logo close --> */}
            {/*<!-- small button begin --> */}
            <span id="menu-btn"></span>
            {/* <!-- small button close --> */}

            <Link to="/contact" className="btn-rsvp">
              Contact
            </Link>

            {/* <!-- mainmenu begin --> */}
            <nav>
              <ul id="mainmenu">
                <li>
                  <Link to="/">Home</Link>
                </li>
                <li>
                  <Link to="/gallery">Gallery</Link>
                  <ul>
                    <li>
                      <Link to="/gallery">Images</Link>
                    </li>
                    <li>
                      <Link to="/gallery">Videos</Link>
                    </li>
                  </ul>
                </li>
                <li>
                  <Link to="/about">About</Link>
                </li>
                <li>
                  <Link to="/services">Services</Link>
                </li>
              </ul>
            </nav>
            {/* <!-- mainmenu close --> */}
          </div>
        </div>
      </div>
    </header>
  );
};

export default Navbar;
