import React from "react";
import { Link } from "react-router-dom";

const Contact = () => {
  return (
    <div>
      {/* <!-- subheader --> */}
      <section
        id="subheader"
        className="subheader_02 dark no-top no-bottom"
        data-stellar-background-ratio=".2"
      >
        <div className="wrap">
          <div className="container">
            <div className="row">
              <div
                className="col-md-12 text-center fadeScroll relative"
                data-scroll-speed="2"
              >
                <h2 data-scroll-speed="8">Contact</h2>
                <div className="small-border"></div>
                <div className="spacer-double"></div>
              </div>
            </div>
          </div>
        </div>
      </section>
      {/* <!-- subheader close--> */}

      {/* <!-- content begin --> */}
      <div id="content" className="no-top no-bottom">
        {/* <!-- section begin --> */}
        <section id="section-contact">
          <div className="container">
            <div className="row">
              <div className="col-md-8">
                <div className="row">
                  <form
                    name="contactForm"
                    id="contact_form"
                    method="post"
                    action=""
                  >
                    <div className="col-md-6">
                      <div className="field-set">
                        <input
                          type="text"
                          name="name"
                          id="name"
                          className="form-control"
                          placeholder="Your Name"
                        />
                        <div className="line-fx"></div>
                      </div>

                      <div className="field-set">
                        <input
                          type="text"
                          name="email"
                          id="email"
                          className="form-control"
                          placeholder="Your Email"
                        />
                        <div className="line-fx"></div>
                      </div>

                      <div className="field-set">
                        <input
                          type="text"
                          name="phone"
                          id="phone"
                          className="form-control"
                          placeholder="Your Phone"
                        />
                        <div className="line-fx"></div>
                      </div>
                    </div>

                    <div className="col-md-6">
                      <div className="field-set">
                        <textarea
                          name="message"
                          id="message"
                          className="form-control"
                          placeholder="Your Message"
                        ></textarea>
                        <div className="line-fx"></div>
                      </div>
                    </div>

                    <div className="spacer-single"></div>

                    <div className="col-md-12">
                      <div id="submit">
                        <input
                          type="submit"
                          id="send_message"
                          value="Submit Form"
                          className="btn btn-custom"
                        />
                      </div>
                      <div id="mail_success" className="success">
                        Your message has been sent successfully.
                      </div>
                      <div id="mail_fail" className="error">
                        Sorry, error occured this time sending your message.
                      </div>
                    </div>
                  </form>
                </div>
              </div>

              <div className="col-md-3 col-md-offset-1">
                <h6 className="id-color">Address</h6>
                <h4>
                  Office No.2, Nilkanth,Sr.No.1297, Shivajinagar, Bh.Joshi
                  Wadewale,Near Balgandhrva Chowk, Pune-411005
                </h4>

                <div className="spacer-single"></div>

                <h6 className="id-color">Call Us</h6>
                <h4>
                  +919850950187 <br />
                  +919850955959
                </h4>

                <div className="spacer-single"></div>

                <h6 className="id-color">Email Us</h6>
                <h4>yojakajevents.entertainment@gmail.com</h4>
              </div>
            </div>
          </div>
        </section>
      </div>
      {/* <!-- content close --> */}

      <Link to="/contact" id="back-to-top" />
      <div id="preloader">
        <div className="preloader1"></div>
      </div>
    </div>
  );
};

export default Contact;
